.MLF_shift-down tbody{
    width:100%;
    height: 100%;
}

.MLF_shift-down th,
.MLF_shift-down td{
    border: 1px solid black;
   height: 80px;
}

.MLF_shift-down{
 width:100%;
    width: 1300px;
     
    border-collapse: collapse;
    margin-left: auto;
    margin-right: auto;
    /* table-layout: fixed; */
}

.MLF_shift-down thead{
    background-color: lightgrey;
}
.MLF_shift-down th{
    color: steelblue;
}
.MLF_shift-down h1{
    font-size: 15pt;
    color: #4B6D90;
    text-align: center;
    padding-top: 20px;
}
.MLF_shift-down h2{
    color: #4B6D90;
}
.MLF_double-space{
    line-height: 1.MLF_5;
}
.MLF_shift-right{
    margin-left: 80px;
}
.MLF_float-right{
    float: right;
    clear: right;
    padding-right: 190px;
    font-size: 13pt;

}
.MLF_FVSRA{

    width: 250px;
    position: absolute;
    left: 90px;
    top: 90px;

}
.MLF_shift-down{
    position: relative;
    top: 190px;
}
.MLF_bottom{
    position: relative;
    left: 0;
    bottom: -300px;
    width: 100%;
    text-align: center;
}
.MLF_submit{
    font-family: "Roboto", sans-serif;
    font-size: 18px;
    width: 300px;
    padding: 16px 32px;
    border: none;
    text-decoration: none;
    text-transform: uppercase;
    color: #fff;
    background-color: lightgrey;
    border-radius: 5px;
    cursor: pointer;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.MLF_1);
    -webkit-transition-duration: 0.MLF_3s;
    transition-duration: 0.MLF_3s;
    -webkit-transition-property: box-shadow, transfrom;
    transition-property: box-shodow, transform;
}
.MLF_submit:hover, .MLF_submit:focus, .MLF_submit:active{
    color: white;
    background-color: #4B6D90;
    border: 1px solid black;
    -webkit-transform: scale(1.MLF_1);
    transform: scale(1.MLF_1);
}
.MLF_shift-down td, .MLF_shift-down tbody, .MLF_shift-down td, .MLF_shift-down input{
    text-align: center;
}
