table.tableMinorInjuryLog{
  border: 1px solid #1C6EA4;
  background-color: #EEEEEE;
  width: 100%;
  text-align: left;
  border-collapse: collapse;
}

table.tableMinorInjuryLog td, table.tableMinorInjuryLog th {
  border: 1px solid #AAAAAA;
  padding: 3px 2px;
}

table.tableMinorInjuryLog tbody td {
  font-size: 13px;
}

table.tableMinorInjuryLog tr:nth-child(even) {
  background: #D0E4F5;
}

table.tableMinorInjuryLog thead {
  background: #1C6EA4;
  background: -moz-linear-gradient(top, #5592bb 0%, #327cad 66%, #1C6EA4 100%);
  background: -webkit-linear-gradient(top, #5592bb 0%, #327cad 66%, #1C6EA4 100%);
  background: linear-gradient(to bottom, #5592bb 0%, #327cad 66%, #1C6EA4 100%);
  border-bottom: 2px solid #444444;
}

table.tableMinorInjuryLog thead th {
  font-size: 15px;
  font-weight: bold;
  color: #FFFFFF;
  border-left: 2px solid #D0E4F5;
}

table.tableMinorInjuryLog thead th:first-child {
  border-left: none;
}

table.tableMinorInjuryLog tfoot {
  font-size: 14px;
  font-weight: bold;
  color: #FFFFFF;
  background: #D0E4F5;
  background: -moz-linear-gradient(top, #dcebf7 0%, #d4e6f6 66%, #D0E4F5 100%);
  background: -webkit-linear-gradient(top, #dcebf7 0%, #d4e6f6 66%, #D0E4F5 100%);
  background: linear-gradient(to bottom, #dcebf7 0%, #d4e6f6 66%, #D0E4F5 100%);
  border-top: 2px solid #444444;
}

table.tableMinorInjuryLog tfoot td {
  font-size: 14px;
}

table.tableMinorInjuryLog tfoot .links {
  text-align: right;
}

table.tableMinorInjuryLog tfoot .links a{
  display: inline-block;
  background: #1C6EA4;
  color: #FFFFFF;
  padding: 2px 8px;
  border-radius: 5px;
}

h2.viewMinorInjuryLogHeader {
  color: black;
  padding: 0.5em;
}


